import React, { useEffect, useState, MouseEvent } from 'react'
import MetadataService from '../../services/Metadata.service';
import Icampaign from '../../models/ICampign';
import CampaignCard from '../../parts/campaignCard';
import ErrorCard from '../../parts/errorCard';


function Campaign() {
    const [campaigns, setCampaigns] = useState<Icampaign[]>([]);
    const [filterTeam, setfilterTeam] = useState<string | null>(null);


    const handleClickTeam = (e: MouseEvent<HTMLButtonElement>) => {
        e.preventDefault();
        let team = e.currentTarget.getAttribute('value');
        console.log(team)
        setfilterTeam(team);
    }
    useEffect(() => {
        MetadataService.getCampaignMissions().then(response => {
            console.dir(response.data)
            setCampaigns(response.data);
        }).catch(error => {console.dir(error)} )
    }, [])


    return (
        <div className="container  px-4">
            <div>
                <button type="button" onClick={handleClickTeam} value="BlueTeam">Blue Team</button>
                <button type="button" onClick={handleClickTeam} value="OsirisTeam">Osiris Team</button>

            </div>
            <ErrorCard/>
            <div className="grid sm:grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-3">
                {
                    campaigns.filter(x => filterTeam !== null ? x.type === filterTeam : x.type !== null).map(Campaign => <CampaignCard name={Campaign.name}
                        missionNumber={Campaign.missionNumber}
                        description={Campaign.description}
                        imageUrl={Campaign.imageUrl}
                        type={Campaign.type}
                        id={Campaign.id} />)
                }

            </div>

        </div>
    )
}

export default Campaign
