import React from 'react'
import Icampaign from '../../models/ICampign';
const CampaignCard: React.FC<Icampaign> = (campaign) => {
    return (
        <>
            <div className="w-full shadow-md rounded-md my-2">
                <div>
                    <img className="h-1/2 " src={campaign.imageUrl} alt="imgcampaign" />
                    <h1 className="bg-gray-400 p-2">{campaign.name} {campaign.missionNumber} {campaign.type}</h1>
                    <p className=" p-2">{campaign.description}</p>
                </div>

            </div>
        </>
    )
}

export default CampaignCard
